<template>
  <div class="Home">
    <Skills :skills="sourceData.skills" />

    <Portfolio :projects="sourceData.portfolio" />

    <Articles :articles="sourceData.articles" />
  </div>
</template>

<script>
import data from '@/data.json'
import Skills from '@/components/Home/Skills'
import Portfolio from '@/components/Home/Portfolio'
import Articles from '@/components/Home/Articles'

export default {
  name: 'home',
  components: { Skills, Portfolio, Articles },
  /* data() {
    return {
      sourceData
    }
  }, */
  computed: {
    sourceData() {
      if (this.$i18n.locale === 'ES') return data.es
      else return data.en
    }
  }
}
</script>
