import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

// Translations
import layout from '../components/Layout/layout.lang'
import home from '../components/Home/home.lang'

export default new VueI18n({
  locale: 'ES',
  messages: {
    EN: {
      layout: layout.en,
      home: home.en
    },
    ES: {
      layout: layout.es,
      home: home.es
    }
  }
})
