<template>
  <!-- Work -->
  <section class="portfolio py-5" id="portfolio">
    <div class="header-section_light">
      <b-container>
        <b-row>
          <b-col>
            <h2 class="header-section__title">
              {{ $t('home.portfolio.title') }}
              <span class="header-section__shadow">
                {{ $t('home.portfolio.header') }}
              </span>
            </h2>
            <div class="header-section__line"></div>
          </b-col>
        </b-row>
      </b-container>
    </div>

    <!-- Filters -->
    <b-container>
      <ul
        class="nav nav-pills nav-fill justify-content-center flex-column flex-md-row text-center text-md-left mb-5 filter"
      >
        <a
          href="#"
          class="nav-link filter__link"
          :class="{ active: currentCategory === 'all' }"
          @click.prevent="projectsByCat('all')"
          >All</a
        >
        <a
          href="#"
          class="nav-link filter__link"
          :class="{ active: currentCategory === category }"
          v-for="(category, index) in filteredCategories"
          :key="index"
          @click.prevent="projectsByCat(category)"
          >{{ category }}</a
        >
      </ul>
    </b-container>

    <transition-group class="portfolio__list" name="projects">
      <div
        class="portfolio__item"
        v-for="project in filteredProjects"
        :key="project.id"
      >
        <figure class="portfolio__figure">
          <b-img
            :src="require(`@/assets/img/portfolio/${project.img_thumb}`)"
            class="portfolio__img"
            alt="project.title"
          ></b-img>
          <figcaption class="portfolio__figcaption">
            <a
              :href="require(`@/assets/img/portfolio/${project.img_thumb}`)"
              class="portfolio__link"
              type="button"
              @click.prevent="$bvModal.show(`modal-project-${project.id}`)"
            ></a>
            <div class="portfolio__inner">
              <h3 class="portofolio__title">{{ project.name }}</h3>
              <p class="portfolio__description">{{ project.short_info }}</p>
            </div>
          </figcaption>
        </figure>

        <!-- Modals -->
        <PortfolioModal :project="project" />
      </div>
    </transition-group>
  </section>
</template>

<script>
import PortfolioModal from '@/components/Home/PortfolioModal'
export default {
  props: {
    projects: {
      type: Array,
      required: true
    }
  },
  components: { PortfolioModal },

  data() {
    return {
      currentCategory: 'all',
      filteredProjects: this.projects
    }
  },

  computed: {
    filteredCategories() {
      const categories = this.projects.map(project => project.category[0])
      return Array.from(new Set(categories))
    }
  },

  methods: {
    projectsByCat(cat) {
      this.currentCategory = cat
      if (cat !== 'all') {
        this.filteredProjects = this.projects.filter(project => {
          if (project.category.includes(cat)) {
            return project
          }
        })
      } else {
        this.filteredProjects = this.projects
      }
    }
  },

  watch: {
    projects() {
      this.filteredProjects = this.projects
    }
  }
}
</script>

<style lang="scss">
.portfolio {
  background-color: $c-dark;

  .nav-pills .nav-link.active {
    background-color: transparent;
    border-bottom: 1px solid $c-primary;
    border-top: 1px solid $c-primary;
    border-radius: 0;
    color: $c-primary;
  }
}

// Filters
.filter__link {
  color: $c-neutro;
  transition: color 0.3s ease-in-out;
  text-transform: capitalize;
}

.filter__link:hover {
  color: $c-primary;
}

.portfolio__list {
  position: relative;
  display: block;
  list-style: none;
  width: 100%;
  padding: 0;
  margin: 0;
}

// Transition
.projects-enter {
  transform: scale(0.5) translatey(-80px);
  opacity: 0;
}

.projects-leave-to {
  transform: translatey(30px);
  opacity: 0;
}

.projects-leave-active {
  position: absolute;
  z-index: -1;
}

.portfolio__item {
  transition: all 0.35s ease-in-out;
  display: block;
  list-style: outside none none;
  margin-bottom: 20px;
  padding: 0 10px;
  width: 100%;

  &:hover {
    .portfolio__figcaption {
      opacity: 1;
      // transform: scale(1);
    }
  }
}

.portfolio__figure {
  margin: 0;
  overflow: hidden;
  position: relative;
}

.portfolio__img {
  display: block;
  width: 100%;
  height: auto;
}

.portfolio__figcaption {
  background: linear-gradient(
    45deg,
    rgba(250, 31, 85, 0.8) 0%,
    rgba(253, 115, 90, 0.8) 100%
  );
  bottom: 8px;
  left: 8px;
  opacity: 0;
  padding: 25px;
  position: absolute;
  right: 8px;
  text-align: center;
  top: 8px;
  transform: scale(1.1);
  transition: all 0.3s ease 0s;
}

.portfolio__link {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  text-indent: 200%;
  z-index: 1000;
}

.portfolio__inner {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  margin-top: -20px;
}

.portofolio__title {
  color: $c-neutro;
  font-size: 1rem;
  font-weight: 400;
  text-transform: uppercase;
}

.portfolio__description {
  color: #ddd;
  font-size: 13px;
  font-style: italic;
}

// Modal styles
.portfolio__modal {
  background-color: #282828 !important;
  color: rgb(202, 202, 202);

  .modal-header {
    border-bottom: 1px solid #3d3d3d;
  }

  .close,
  .close:hover,
  .close:focus {
    color: $c-neutro;
    outline: none;
  }

  .modal-footer {
    border-top: 1px solid #3d3d3d;
  }
}

.portfolio__tags {
  margin-top: 1rem;

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  li {
    margin: 0px 6px;
    border-radius: 4px;
    margin-bottom: 10px;
    padding: 4px 0px;
    display: inline-block;
  }

  span {
    border: 1px solid $c-primary;
    padding: 4px 10px;
    border-radius: 4px;
    font-size: 0.8rem;
    text-transform: lowercase;
  }
}

.portfolio__btn {
  background-color: $c-primary !important;
  border-radius: 2rem !important;
  color: $c-neutro !important;
  cursor: pointer !important;
  margin-top: 1rem;
}

@media screen and (min-width: 768px) {
  .portfolio__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  .portfolio__item {
    width: 50%;
  }
}

@media screen and (min-width: 992px) {
  .portfolio__item {
    width: 33.33%;
  }
}
</style>
