<style lang="scss">
.header {
  background: url('~@/assets/img/bg-header.jpg') no-repeat 0 0;
  background-size: cover;
  background-attachment: fixed;
  min-height: 100vh;

  .nav-link {
    color: $c-neutro !important;
    transition: color 0.3s ease;
  }

  .nav-link:hover,
  .nav-link.active {
    // border-bottom: 1px solid $c-primary;
    color: $c-primary !important;
  }
}

.header__navbar {
  background-color: $c-dark;
  min-height: 60px;
}

.header__logo {
  max-width: 100px;
}

.header__name {
  font-family: 'Lobster', cursive;
  font-size: 1.5rem;
  margin-left: -1rem;
}

.header__toggler-btn .fas {
  color: $c-primary;
}

.header__menu {
  align-items: center;
}

.nav-link.header__link {
  font-size: 1.1rem;
}

// Hero
.hero {
  color: $c-dark;
  min-height: 100vh;
  padding-top: 120px;
  position: relative;
}

.hero__content {
  border-left: 0.5rem solid $c-secondary;
  padding: 2.5rem 0.5rem 2.5rem 1.5rem;
}

.hero__title {
  font-size: 1.75rem;
  margin-bottom: 1rem;
}

.hero__name {
  color: $c-primary;
  font-family: 'Lobster', cursive;
  font-size: 1.75rem;
}

.hero__subtitle {
  font-size: 1.5rem;
  font-weight: 300;
}

.hero__img {
  border: 2px solid $c-gray;
  border-radius: 0.5rem;
  width: 90px;
}

.hero__btn {
  background-color: transparent;
  border: 2px solid $c-secondary;
  border-radius: 50%;
  color: $c-dark;
  cursor: pointer;
  width: 50px;
  height: 50px;
  line-height: 50px;
  margin-bottom: 1.5rem;
}

.hero__btn {
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  transform-origin: bottom;
  animation-name: bouncing;
  animation-timing-function: linear;
  text-decoration: none;
}

@keyframes bouncing {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

.hero__btn::before {
  color: $c-gray;
  font-size: 2rem;
}

// Header Section
.header-section {
  text-align: center;
}

.header-section__title {
  position: relative;
  color: $c-gray;
  font-size: 1.5rem;
  font-weight: 300;
  text-transform: uppercase;
  margin-bottom: 50px;
}

.header-section__shadow {
  font-size: 2.5rem;
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  top: -10px;
  z-index: 0;
  opacity: 0.05;
  font-weight: 700;
}

.header-section__line {
  background: #666;
  width: 55px;
  height: 1px;
  margin: 30px auto;
}

.header-section_light {
  text-align: center;

  .header-section__title {
    color: $c-gray-light;
    text-transform: uppercase;
  }

