<template>
  <b-modal
    :id="`modal-project-${project.id}`"
    size="xl"
    :title="project.name"
    content-class="portfolio__modal"
    header-class="portfolio__modal-header"
  >
    <b-container fluid>
      <b-row>
        <b-col cols="lg-4 pb-4">
          <p>{{ project.description }}</p>
          <div class="portfolio__tags">
            <ul>
              <li v-for="(cat, index) in project.category" :key="index">
                <span>{{ cat }}</span>
              </li>
            </ul>
          </div>
          <a
            :href="project.url"
            target="_blank"
            class="btn portfolio__btn"
            @click="$bvModal.hide(`modal-project-${project.id}`)"
          >
            <span class="mr-1">Go!</span>
            <i class="fas fa-caret-right"></i>
          </a>
        </b-col>
        <b-col cols="lg-8">
          <img
            :src="require(`@/assets/img/portfolio/${project.screenshot}`)"
            :alt="project.name"
            class="img-fluid"
          >
        </b-col>
      </b-row>
    </b-container>

    <template slot="modal-footer" slot-scope="{ ok, cancel }">
      <!-- Emulate built in modal footer ok and cancel button actions -->
      <b-container fluid class="text-right">
        <b-button size="sm" variant="danger" @click="cancel()">Cancel</b-button>
      </b-container>
    </template>
  </b-modal>
</template>

<script>
export default {
  props: {
    project: {
      type: Object,
      required: true
    }
  }
}
</script>

<style lang="scss" scoped>
</style>